import React from 'react';
import { FormLayout, Div, Input, Group, Button, PanelHeader, Panel, Textarea, Select, File } from '@vkontakte/vkui';
import Icon24Camera from '@vkontakte/icons/dist/24/camera';


const requestURL = 'https://ambassador-todo.herokuapp.com/event'

function sendRequest(method, url, body) {
	const headers = {
		'Content-Type': 'application/json'
	}
	return fetch(url, { method: method, headers: headers, body }).then(response => {
		return response.text()
	})
}
